// create-sprite-at-runtime.js
var CreateSpriteAtRuntime = pc.createScript('createSpriteAtRuntime');


CreateSpriteAtRuntime.attributes.add('playerimage', {
    type: 'string',
    title: 'Player image'
});
CreateSpriteAtRuntime.attributes.add('change', {
    type: 'boolean',
    default: false,
    title: 'change'
});



// initialize code called once per entity
CreateSpriteAtRuntime.prototype.initialize = function() {


};

// update code called every frame
CreateSpriteAtRuntime.prototype.update = function(dt) {

};

CreateSpriteAtRuntime.prototype.successRef = function(){
    const url = this.playerimage;
    
    const cloudLogo = this.entity.findByName('Player');

    const image = new Image();
    image.crossOrigin = 'anonymous';
    const prom = new Promise(res => {
        image.onload = () => {
            const atlas = new pc.TextureAtlas();
            atlas.frames = {
                "0": {
                    rect: new pc.Vec4(0, 0, 64, 64),
                    pivot: new pc.Vec2(0.5, 0.5),
                    border: new pc.Vec4(0, 0, 0, 0)
                },

            };
            const texture = new pc.Texture(this.app.graphicsDevice, {
                magFilter: pc.FILTER_NEAREST,
                minFilter: pc.FILTER_NEAREST,
                

            });
                    
            texture.setSource(image);
            atlas.texture = texture;

            const sprite = new pc.Sprite(this.app.graphicsDevice, {
                atlas,
                frameKeys: '0',
                pixelsPerUnit: 50,
                renderMode: pc.SPRITE_RENDERMODE_SIMPLE

            });
            return res(sprite);
        };
        image.src = url;
    });

    prom
        .then(sprite=>{
        cloudLogo.sprite.sprite = sprite;
     
    });

   
};


