// Movement.js
var Movement = pc.createScript('movement');

Movement.attributes.add('playerSpeed', {
    type: 'number',
    default: 30,
    title: 'Player Speed'
});


// initialize code called once per entity
Movement.prototype.initialize = function() {
    this.force = new pc.Vec3();
    /*衝突した際のイベントハンドラ*/
    this.entity.collision.on("contact",this._colstart,this);
    this.entity.collision.on("collisionend",this._colend,this);
 
};

// update code called every frame
Movement.prototype.update = function(dt) {
    var forward = this.entity.forward;
    var right = this.entity.right;
    var app = this.app;

    x = 0;
    z = 0;

    if (app.keyboard.isPressed(pc.KEY_A)) {
        x -= right.x;
        z -= right.z;
      
        this.movefor = 1;
    }

    if (app.keyboard.isPressed(pc.KEY_D)) {
        x += right.x;
        z += right.z;
     
        this.movefor = -1;
    }

    if (app.keyboard.isPressed(pc.KEY_W)) {
        x += forward.x;
        z += forward.z;
    }

    if (app.keyboard.isPressed(pc.KEY_S)) {
        x -= forward.x;
        z -= forward.z;
    }

    if (x !== 0 || z !== 0) {
        x *= dt;
        z *= dt;

        this.force.set (x, 0, z).normalize ().scale ((this.playerSpeed));
        this.entity.rigidbody.applyForce (this.force);
    }
    this.setDirection(this.movefor);
};

/*ベクトルに応じてspriteの向きを変更するメソッド　引数:x軸の移動量*/
Movement.prototype.setDirection = function(accu) {
    this.entity.sprite.flipX = (accu < 0);
};

Movement.prototype._colstart = function(event) {
   if(event.other.tags.has("kabe")){
    // 呼び出したいイベントが登録されているか
    if( this.app.hasEvent('colliEvent') ){
       
        // イベントを発火させる
        this.app.fire('colliEvent');
    }
   }
};
Movement.prototype._colend = function(event) {
  
    // 呼び出したいイベントが登録されているか
    if( this.app.hasEvent('colliEnd') ){
       
        // イベントを発火させる
        this.app.fire('colliEnd');
    }
   
};





