// ChatUtility.js
var ChatUtility = pc.createScript('chatUtility');

ChatUtility.attributes.add('fontSize', { type : 'number', default : 1 });
ChatUtility.attributes.add('padding', { type : 'number', default : 1 });
ChatUtility.attributes.add('scaleUnit', {
    type: 'string',
    enum: [
        { 'Viewport Width': 'vw' },
        { 'Viewport Height': 'vh' },
        { 'Pixel': 'px' }
    ],
    default : 'vw'
});
ChatUtility.attributes.add('color', { type : 'rgb' });
ChatUtility.attributes.add('fontFamily', { type : 'string', default : 'Arial, sans-serif' });
ChatUtility.attributes.add('storeValue', { type : 'boolean' });

ChatUtility.attributes.add('focusEntity', { type : 'entity' });

ChatUtility.prototype.initialize = function() {
    this.element = document.createElement('div');

    
    this.element.style.position = 'absolute';
    this.element.style.fontFamily = this.fontFamily;
    
    this.element.style.border = '0px';
    this.element.style.borderRadius = '5px';
    this.element.style.background = 'rgb(' + (this.color.r * 167) + ', ' + (this.color.g * 167) + ', ' + (this.color.b * 167) + ')';
    
    this.element.style.fontSize = this.fontSize + this.scaleUnit;
    this.element.style.padding = this.padding + this.scaleUnit;
    this.element.style.paddingLeft = 60 + this.scaleUnit;
    this.element.style.boxSizing = 'border-box';
    
    var color = 'rgb(' + (this.color.r * 0) + ', ' + (this.color.g * 0) + ', ' + (this.color.b * 0) + ')';
    this.element.style.color = color;
    
    this.element.style.outline = 'none';
    document.body.appendChild(this.element);
    
    //disable focus entity
    if(this.focusEntity){
        this.focusEntity.enabled = false;   
        
        this.element.onfocus = this.onFocus.bind(this);
        this.element.onblur = this.onBlur.bind(this);
    }
    
    this.element.onchange = this.onChange.bind(this);
    
    if(this.storeValue){
        var value = window.localStorage.getItem(this.entity._guid);
        
        if(value){
            this.setValue(window.localStorage.getItem(this.entity._guid));   
        }
    }
    
    this.updateStyle();
    
    this.on('state', function(self){
        if(this.entity.enabled){
            this.element.style.display = 'block'; 
        }else{
            this.element.style.display = 'none'; 
        }
    }, this);
};

ChatUtility.prototype.onFocus = function() {
    this.focusEntity.enabled = true;
};

ChatUtility.prototype.onBlur = function() {
    this.focusEntity.enabled = false;
};

ChatUtility.prototype.onChange = function() {
    if(this.storeValue){
        window.localStorage.setItem(this.entity._guid, this.getValue());
    }
};

ChatUtility.prototype.updateStyle = function() {
    if(this.entity.element.screenCorners){
        var position = this.entity.element.screenCorners;
        var devicePixelRatio = this.app.graphicsDevice.maxPixelRatio;
    
        this.element.style.left = (position[0].x / devicePixelRatio) + 'px';
        this.element.style.bottom = (position[0].y /devicePixelRatio) + 'px';

        this.element.style.width = ((position[2].x - position[0].x) / devicePixelRatio) + 'px';
        this.element.style.height = ((position[2].y - position[0].y) / devicePixelRatio) + 'px';
    }
};

ChatUtility.prototype.update = function(dt) {
    this.updateStyle();
};

ChatUtility.prototype.setValue = function(value) {
    this.element.value = value;
};

ChatUtility.prototype.getValue = function() {
    if(this.element){
        return this.element.value;   
    }
};

