// CameMove.js
var CameMove = pc.createScript('cameMove');

CameMove.attributes.add('playerSpeed', {
    type: 'number',
    default: 30,
    title: 'Camera Speed'
});
CameMove.attributes.add('stop', {
    type: 'boolean',
    default: false,
    title: 'stop'
});

// initialize code called once per entity
CameMove.prototype.initialize = function() {
    this.force = new pc.Vec3();

    // イベントリスナーを登録する
    this.app.on('colliEvent', this.colli, this);
    this.app.on('colliEnd', this.collen, this);

    // このEntityが破棄された時にリスナーの登録を解除する(やらないとメモリリークを起こす)
    this.on('destroy', function() {
        this.app.off('colliEvent', this.colli, this);
    });
     this.on('destroy', function() {
        this.app.off('colliEnd', this.collen, this);
    });
};

// update code called every frame
CameMove.prototype.update = function(dt) {
     var forward = this.entity.forward;
    var right = this.entity.right;
    var app = this.app;

    x = 0;
    z = 0;



    if (app.keyboard.isPressed(pc.KEY_A) && (!this.stop)) {
        x -= right.x;
        z -= right.z;
    }

    if (app.keyboard.isPressed(pc.KEY_D) && (!this.stop)) {
        x += right.x;
        z += right.z;
    }

    // if (app.keyboard.isPressed(pc.KEY_W) && (!this.stop)) {
    //    x += forward.x;
    //    z += forward.z;
    //}

    // if (app.keyboard.isPressed(pc.KEY_S) && (!this.stop)) {
    //    x -= forward.x;
    //    z -= forward.z;
    //}
    

    if (x !== 0 || z !== 0 && (!this.stop)) {
        x *= dt;
        z *= dt;

        this.force.set (x, 0, z).normalize ().scale ((this.playerSpeed));
        this.entity.rigidbody.applyForce (this.force);
        }
    
};

// 呼び出すメソッド

CameMove.prototype.colli = function(){
    
    this.stop = true;
};
CameMove.prototype.collen = function(){
    console.log("out");//コンソールにhitと表示
    this.stop = false;
};

