// Network.js
var Network = pc.createScript('network');



var otherimage = null;
var chan = false;
 console.log(this.chan);
// initialize code called once per entity
Network.prototype.initialize = function() {
    this.socket = io.connect('https://abaft-jumbled-dibble.glitch.me/');

        this.socket.emit ('initialize');
var socket = this.socket;

this.player = this.app.root.findByName ('Player');
this.other = this.app.root.findByName ('Other');

var self = this;
socket.on ('playerData', function (data) {
    self.initializePlayers (data);
});

socket.on ('playerJoined', function (data) {
    self.addPlayer (data);
});
socket.on ('playerMoved', function (data) {
    self.movePlayer (data);

});
socket.on ('otherImg', function (data) {
    self.successRef (data);
});
socket.on ('imgFlag', function () {
    self.thischan ();
});
socket.on ('killPlayer', function (data) {
    self.deletePlayer (data);
});
};


// update code called every frame
Network.prototype.update = function (dt) {
    this.updatePosition ();
   
    
};

Network.prototype.imagechange = function (data){
             
             
             const url = data.img;
    
    const othim = this.players[data.id].entity;

    const image = new Image();
    image.crossOrigin = 'anonymous';
    const prom = new Promise(res => {
        image.onload = () => {
            const atlas = new pc.TextureAtlas();
            atlas.frames = {
                "0": {
                    rect: new pc.Vec4(0, 0, 64, 64),
                    pivot: new pc.Vec2(0.5, 0.5),
                    border: new pc.Vec4(0, 0, 0, 0)
                },

            };
            const texture = new pc.Texture(this.app.graphicsDevice, {
                magFilter: pc.FILTER_NEAREST,
                minFilter: pc.FILTER_NEAREST,
                

            });
                    
            texture.setSource(image);
            atlas.texture = texture;

            const sprite = new pc.Sprite(this.app.graphicsDevice, {
                atlas,
                frameKeys: '0',
                pixelsPerUnit: 50,
                renderMode: pc.SPRITE_RENDERMODE_SIMPLE

            });
            return res(sprite);
        };
        image.src = url;
    });

    prom
        .then(sprite=>{
        othim.sprite.sprite = sprite;
    
    });

};

Network.prototype.initializePlayers = function (data) {
    this.players = data.players;
    // Create a player array and populate it with the currently connected players.

    this.id = data.id;
    // Keep track of what ID number you are.

    for(var id in this.players){
        if(id != Network.id){
            this.players[id].entity = this.createPlayerEntity(this.players[id]);
            if(data.img){
            this.imagechange(data);
            }
        }
    }
    // For every player already connected, create a new capsule entity.

    this.initialized = true;
    // Mark that the client has received data from the server.
};

Network.prototype.createPlayerEntity = function (data) {
    var newPlayer = this.other.clone ();
    // Create a new player entity.

    newPlayer.enabled = true;
    // Enable the newly created player.

    this.other.getParent ().addChild (newPlayer);
    // Add the entity to the entity hierarchy.

    if (data)
        newPlayer.rigidbody.teleport (data.x, data.y, data.z);
    // If a location was given, teleport the new entity to the position of the connected player.

    return newPlayer;
    // Return the new entity.
};

Network.prototype.addPlayer = function (data) {
    this.players[data.id] = data;
    this.players[data.id].entity = this.createPlayerEntity(data);
};

Network.prototype.thischan = function () {
    this.chan = false;
};
Network.prototype.otherchange = function () {
    this.chan = true;
};



Network.prototype.movePlayer = function (data) {
    if (this.initialized)
        this.players[data.id].entity.rigidbody.teleport (data.x, data.y, data.z);
        if(data.dir < 0){
            this.players[data.id].entity.sprite.flipX = false;
        }else{
            this.players[data.id].entity.sprite.flipX = true;
        }



            if(data.imflag === true){
        
       this.imagechange(data);
    }
    
       
};




Network.prototype.updatePosition = function () {
    if (this.initialized) {
        var pos = this.player.getPosition ();
        var dir = dir;
        if(this.player.sprite.flipX=== false){
           this.dir =  -1;
    }else{
        this.dir =  1;
    }
        
        this.socket.emit ('positionUpdate', {id: this.id, x: pos.x, y: pos.y, z: pos.z, dir: this.dir, img: this.otherimage, imflag: this.chan});
    }
   
};



Network.prototype.deletePlayer = function (data) {
     if (this.players[data].entity) {
        this.players[data].entity.destroy ();
        this.players[data].deleted = true;
    }
    
};
