// chatform.js

/*jshint esversion: 6, asi: true, laxbreak: true*/
class Chatform extends  pc.ScriptType {
  initialize(){
      this.hideElement = {onclick: ()=> {this.hide()}}
      this.show()
  }

  show(){
      const component = m("div",
  [
   
    m("div", {"class":"relative w-1920 h-1080 rounded-md bg-sky-500 opacity-25"},
      [
        
        m("input", {"class":"absolute top:100px w-96 h-20 rounded-md border-gray-300 pl-7 pr-12 focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm","type":"text","name":"price","id":"price","placeholder":"0.00"}),
  
      ]
    )
  ]
)
      this.app.fire("render", component)
  }

  hide(){
      this.app.fire("clear")
  }
}

pc.registerScript(Chatform)

