// Chara0.js
var Chara0 = pc.createScript('chara0');

Chara0.attributes.add('purl', {
    type: 'string',
    default: "https://raw.githubusercontent.com/Shashop/LLtest/main/deric.png",
    title: 'purl'
});

Chara0.attributes.add('logEntity', {type: 'entity', title: 'Log Entity'});

// initialize code called once per entity
Chara0.prototype.initialize = function() {

this.entity.button.on('click', function(event) {
      var logScript  = this.logEntity.findComponent('script');

   logScript.createSpriteAtRuntime.playerimage = this.purl;
   logScript.createSpriteAtRuntime.successRef();
   logScript.network.otherimage = this.purl;
   logScript.network.otherchange();
   
}, this);

    this.entity.button.on('click', function(event) {
    // 呼び出したいイベントが登録されているか
    if( this.app.hasEvent('imachange') ){
       
        // イベントを発火させる
        this.app.fire('imachange');
        
    }
}, this);
    
};


// update code called every frame
Chara0.prototype.update = function(dt) {
    
};

